'use client'

import Image from 'next/image'
import { useRef } from 'react'
import { motion, useScroll, useTransform } from 'framer-motion'

export default function Hero() {
  const sectionRef = useRef<HTMLElement>(null)
  
  const { scrollYProgress } = useScroll({
    target: sectionRef,
    offset: ['start start', 'end start'],
  })

  // Text fades out and moves up as you scroll
  const textOpacity = useTransform(scrollYProgress, [0, 0.5], [1, 0])
  const textY = useTransform(scrollYProgress, [0, 0.5], [0, -50])
  
  // Image subtle zoom out
  const imageScale = useTransform(scrollYProgress, [0, 1], [1, 1.1])
  
  // Parallax for gradient blurs - each moves at different speed
  const blur1Y = useTransform(scrollYProgress, [0, 1], [0, 200])
  const blur2Y = useTransform(scrollYProgress, [0, 1], [0, 150])
  const blur3Y = useTransform(scrollYProgress, [0, 1], [0, 250])
  const blur4Y = useTransform(scrollYProgress, [0, 1], [0, 180])

  return (
    <section ref={sectionRef} className="relative min-h-screen overflow-hidden" style={{ backgroundColor: '#1A1A1A' }}>
      {/* Decorative gradient blurs with parallax */}
      <motion.div 
        className="gradient-blur-1 absolute w-[600px] h-[600px] -left-[100px] top-[10%] opacity-60 z-[1]" 
        style={{ y: blur1Y }}
      />
      <motion.div 
        className="gradient-blur-2 absolute w-[600px] h-[600px] left-[200px] top-[35%] opacity-50 z-[1]" 
        style={{ y: blur2Y }}
      />
      <motion.div 
        className="gradient-blur-3 absolute w-[360px] h-[324px] left-[11px] top-[28%] z-[1]" 
        style={{ y: blur3Y }}
      />
      <motion.div 
        className="gradient-blur-4 absolute w-[600px] h-[600px] left-[350px] top-[50%] opacity-40 z-[1]" 
        style={{ y: blur4Y }}
      />

      {/* Profile image - flush to right edge with zoom effect */}
      <div className="hidden lg:block absolute right-0 top-0 w-[48%] h-full z-[2] overflow-hidden">
        <motion.div 
          className="w-full h-full"
          style={{ scale: imageScale }}
        >
          <Image
            src="/images/profile.jpg"
            alt="Elizabeth Zhu"
            fill
            className="object-cover object-top"
            style={{ borderTopLeftRadius: '40px', borderBottomLeftRadius: '40px' }}
            priority
          />
        </motion.div>
      </div>

      {/* Content container */}
      <div className="relative z-10 min-h-screen flex flex-col px-6 lg:px-[100px]">
        {/* Desktop - vertically centered content with scroll animation */}
        <motion.div 
          className="hidden lg:flex flex-1 items-center"
          style={{ opacity: textOpacity, y: textY }}
        >
          <div className="lg:w-[50%] lg:max-w-[608px]">
            <motion.p 
              className="font-serif text-text-light text-2xl mb-12"
              initial={{ opacity: 0, y: 30 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.8, delay: 0.2, ease: [0.25, 0.4, 0.25, 1] }}
            >
              Elizabeth Zhu
            </motion.p>
            
            <motion.h1 
              className="font-sans text-text-light text-[28px] leading-relaxed" 
              style={{ fontWeight: 400 }}
              initial={{ opacity: 0, y: 30 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.8, delay: 0.4, ease: [0.25, 0.4, 0.25, 1] }}
            >
              I&apos;m a Senior Product Designer at Meta.
              <br /><br />
              My work is grounded in empathy and problem solving, and shaped by continuous feedback and collaboration.
            </motion.h1>
          </div>
        </motion.div>

        {/* Mobile content */}
        <div className="lg:hidden flex flex-col flex-1">
          <div className="flex flex-col justify-center py-24 flex-1">
            <motion.p 
              className="font-serif text-text-light text-xl mb-4"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.6, delay: 0.2 }}
            >
              Elizabeth Zhu
            </motion.p>
            
            <motion.h1 
              className="font-sans text-text-light text-[28px] leading-relaxed" 
              style={{ fontWeight: 400 }}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.6, delay: 0.4 }}
            >
              I&apos;m a Senior Product Designer at Meta.
              <br /><br />
              My work is grounded in empathy and problem solving, and shaped by continuous feedback and collaboration.
            </motion.h1>
          </div>

          {/* Mobile - Footer text above photo */}
          <motion.p 
            className="pb-6 font-sans text-text-light text-sm"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ duration: 0.6, delay: 0.6 }}
          >
            Website built with <span className="font-semibold">Cursor AI</span>
          </motion.p>

          {/* Mobile - Profile image */}
          <div className="relative flex-1 min-h-[400px] -mx-6">
            <Image
              src="/images/profile.jpg"
              alt="Elizabeth Zhu"
              fill
              className="object-cover object-top"
              priority
            />
          </div>
        </div>

        {/* Desktop - Footer text at bottom */}
        <motion.p 
          className="hidden lg:block pb-[97px] font-sans text-text-light text-base"
          style={{ opacity: textOpacity }}
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 0.6, delay: 0.6 }}
        >
          Website built with <span className="font-semibold">Cursor AI</span>
        </motion.p>
      </div>
    </section>
  )
}
