'use client'

import { useState } from 'react'
import CopyIcon from './icons/CopyIcon'
import ExternalLinkIcon from './icons/ExternalLinkIcon'

export default function Contact() {
  const [copied, setCopied] = useState(false)
  const email = 'elizabethzhulim@gmail.com'

  const handleCopyEmail = async () => {
    try {
      await navigator.clipboard.writeText(email)
      setCopied(true)
      setTimeout(() => setCopied(false), 2000)
    } catch (err) {
      console.error('Failed to copy email:', err)
    }
  }

  return (
    <section className="bg-background py-20 lg:py-32 pb-32 lg:pb-48">
      <div className="px-6 lg:px-[100px]">
        <h2 className="section-heading mb-8 lg:mb-[144px]">Contact</h2>

        <div className="flex flex-col gap-4">
          {/* Email */}
          <div className="flex items-center gap-4">
            <button
              onClick={handleCopyEmail}
              className="flex items-center gap-4 group cursor-pointer"
            >
              <span className="font-sans font-semibold text-text-dark text-xl lg:text-[28px] group-hover:text-text-dark-alt transition-colors">
                {email}
              </span>
              <div className="relative">
                <CopyIcon className="text-text-dark group-hover:text-text-dark-alt transition-colors w-6 h-6 lg:w-8 lg:h-8" />
                {copied && (
                  <span className="absolute -top-8 left-1/2 -translate-x-1/2 bg-text-dark text-white text-xs px-2 py-1 rounded whitespace-nowrap">
                    Copied!
                  </span>
                )}
              </div>
            </button>
          </div>

          {/* LinkedIn */}
          <a
            href="https://www.linkedin.com/in/elizabethzhulim/"
            target="_blank"
            rel="noopener noreferrer"
            className="flex items-center gap-2 group w-fit"
          >
            <span className="font-sans font-semibold text-text-dark text-xl lg:text-[28px] group-hover:text-text-dark-alt transition-colors">
              Linkedin
            </span>
            <ExternalLinkIcon className="text-text-dark group-hover:text-text-dark-alt transition-colors w-6 h-6 lg:w-8 lg:h-8" />
          </a>
        </div>
      </div>
    </section>
  )
}
